import type { FC } from 'react';
import { Link, graphql } from 'gatsby';
import { Card, FlexRow, FlexColumn } from '../components';
import routes from '../routes';
import { withCommonLayout } from '../hoc';
import { PageProps } from '../types';

interface Post {
  edges: {
    node: {
      frontmatter: {
        projectId: string;
        projectName: string;
        projectImage: {
          childImageSharp: {
            gatsbyImageData: {
              layout: string;
              backgroundColor: string;
              images: {
                fallback: {
                  src: string;
                  srcSet: string;
                  sizes: string;
                };
                sources: {
                  srcSet: string;
                  type: string;
                  sizes: string;
                }[]
              };
              width: number;
              height: number;
            };
          };
        };
      };
    };
  }[];
  totalCount: number;
}

interface IndexPageData {
  allMarkdownRemark: Post;
}

const IndexPage: FC<PageProps<IndexPageData>> = ({ data }) => {
  const { allMarkdownRemark: { edges: projects } } = data;

  return (
    <FlexRow>
      {
        projects.map(({ node: { frontmatter: info } }, index) => (
          <FlexColumn xs={12} sm={6} md={6} lg={4} key={info.projectId}>
            <Link to={`${routes.projectDetailsRoot}/${info.projectId}`} state={{ isProject: true }}>
              <Card
                image={info.projectImage.childImageSharp.gatsbyImageData.images.fallback.src}
                imageAlt={info.projectName}
                imageAspectRatio="40/21"
                imageLazyLoad={index > 3}
              >
                {info.projectName}
              </Card>
            </Link>
          </FlexColumn>
        ))
      }
    </FlexRow>
  );
};

export default withCommonLayout(IndexPage);

export const pageQuery = graphql`
  {
    allMarkdownRemark(
      filter: {fields: {collection: {eq: "projects"}}}
      sort: { fields: [frontmatter___projectDate], order: DESC }
    ) {
      edges {
        node {
          frontmatter {
            projectId
            projectName
            projectImage {
              childImageSharp {
                gatsbyImageData(
                  layout: FULL_WIDTH
                  quality: 100
                )
              }
            }
            projectDate
          }
        }
      }
      totalCount
    }
  }
`;
